:root {
  --primary-color: #282a36;
  --secondary-color: #44475a;
  --selection-color: #44475a;
  --foreground-color: #f8f8f2;
}

html, #root {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  
  background: -webkit-gradient(linear, left top, left bottom, from(#333), to(#aaa));
  
  background: -webkit-linear-gradient(#333, #aaa);
  
  background: -o-linear-gradient(#333, #aaa);
  
  background: linear-gradient(#333, #aaa);
  background-attachment: fixed;
}

a {
  text-decoration: none;
}

h2, h3, h4, h5 {
  font-family: 'Francois One', sans-serif;
}

ul {
  padding: 0;
}



